.input {
  border: none;
  background-color: #000;
  color: #fff;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding-left: 26px;
  box-sizing: border-box;
  width: 100%;
  height: 52px;
  transition: all .4s ease;
  border-radius: 5px;


  @media screen and (max-width: 1023px) {
    height: 40px;
    font-size: 14px;
    padding-left: 15px;
  }

  &:focus-visible {
    outline: none;
  }
}